/* eslint-disable react/jsx-no-duplicate-props */
/* eslint-disable react/no-unused-prop-types */
import { css, SerializedStyles } from '@emotion/react';
import { Editor } from '@tiptap/core';
import { EditorContent, useEditor } from '@tiptap/react';
import { FC, useEffect, useRef, useState } from 'react';

import { Color } from '@tiptap/extension-color';
import FontFamily from '@tiptap/extension-font-family';
import HardBreak from '@tiptap/extension-hard-break';
import Image from '@tiptap/extension-image';
import Link from '@tiptap/extension-link';
import Table from '@tiptap/extension-table';
import TableCell from '@tiptap/extension-table-cell';
import TableHeader from '@tiptap/extension-table-header';
import TableRow from '@tiptap/extension-table-row';
import TextAlign from '@tiptap/extension-text-align';
import TextStyle from '@tiptap/extension-text-style';
import UnderLine from '@tiptap/extension-underline';
import StarterKit from '@tiptap/starter-kit';
import uploadImage from '../../@utils/uploadImage';
import AtomButton from '../AtomButton';
import AtomIcon from '../AtomIcon';
import AtomInput from '../AtomInput';
import AtomLoader from '../AtomLoader';
import AtomText from '../AtomText';
import AtomWrapper from '../AtomWrapper';
import Iframe from './iframe';
import { ButtonStyled, GlobalStyles, InputColorStyled } from './styled';
import { AtomTextEditorOptions, AtomTextEditorType } from './types';
import VideoCustom from './video';

HardBreak.configure({
  keepMarks: false,
});

type MenuBarType = {
  editor?: Editor | null;
  options?: AtomTextEditorOptions;
  onCustomCSSMenuBar?: () => SerializedStyles;
};

const MenuBar: FC<MenuBarType> = (props) => {
  const { editor } = props;
  const [isOpen, setIsOpen] = useState(false);
  const [loadingFile, setLoadingFile] = useState(false);
  const [image, setImage] = useState({} as File | string);
  const [isOpenUrl, setIsOpenUrl] = useState(false);
  const [url, setUrl] = useState('');

  const ref = useRef<HTMLButtonElement>(null);
  const ref2 = useRef<HTMLButtonElement>(null);
  const ref3 = useRef<HTMLButtonElement>(null);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (ref.current && !ref.current.contains(event.target as Node)) {
        setIsOpenUrl(false);
      }
      if (ref2.current && !ref2.current.contains(event.target as Node)) {
        setIsOpen(false);
      }
    };

    document.addEventListener(`mousedown`, handleClickOutside, true);
    return () => {
      document.removeEventListener(`mousedown`, handleClickOutside, true);
    };
  }, [ref, ref2, ref3]);

  const { notIncludeSelectFonts, notincludeTableOptions } =
    props?.options ?? {};

  return (
    <>
      <AtomWrapper
        className="menu-editor"
        flexDirection="row"
        width="100%"
        justifyContent="flex-start"
        alignItems="center"
        flexWrap="wrap"
        padding="0px 0px 0px 0px"
        customCSS={css`
          gap: 5px;
          flex-wrap: wrap;
          padding: 8px 30px;
          ButtonStyled {
            flex-basis: 40px;
            flex-grow: 1;
            width: 40px;
            height: 40px;
            font-size: 15px;
            overflow: hidden;
            background-color: #fafafa;
            border: none;
            color: #373737;
            font-weight: bold;
            box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
          }
          ${props?.onCustomCSSMenuBar?.()}
        `}
      >
        <ButtonStyled
          customCSS={css`
            position: relative;
          `}
          onClick={() => {
            setIsOpen(true);
          }}
        >
          <AtomIcon
            height="16px"
            width="16px"
            color="#373737"
            icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/picture-image-svgrepo-com.svg"
          />
          {isOpen && (
            <AtomWrapper
              refObject={ref2}
              customCSS={css`
                position: absolute;
                background-color: #ffffff;
                padding: 30px 30px;
                border-radius: 5px;
                height: max-content;
                width: 400px;
                max-width: 400px;
                top: 170%;
                left: -50%;
                flex-direction: column;
                justify-content: space-between;
                align-items: center;
                box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.227);
                :before {
                  content: '';
                  z-index: 100;
                  position: absolute;
                  top: -10px;
                  left: 5%;
                  width: 0;
                  height: 0;
                  border-left: 10px solid transparent;
                  border-right: 10px solid transparent;
                  border-bottom: 10px solid #ffffff;
                }
              `}
            >
              {loadingFile ? (
                <AtomLoader
                  isLoading
                  colorLoading="#000000"
                  height="200px"
                  type="small"
                />
              ) : (
                <>
                  <AtomInput
                    height="150px"
                    labelWidth="100%"
                    width="100%"
                    type="dragdrop"
                    onChangeDrop={(e) => {
                      setImage(e);
                    }}
                  />

                  <AtomWrapper
                    margin="10px"
                    customCSS={css`
                      ${typeof image === 'object' &&
                      image.name &&
                      css`
                        span {
                          color: #a0a0a0;
                        }
                        input {
                          background-color: #fcfcfc;
                        }
                      `}
                    `}
                  >
                    <AtomText
                      fontSize="12px"
                      fontWeight={600}
                      margin="0px 15px 0px 0px"
                    >
                      Url:
                    </AtomText>
                    <AtomInput
                      customCSS={css`
                        span {
                          display: none;
                        }
                      `}
                      labelWidth="100%"
                      width="100%"
                      type="text"
                      onChange={(e) => {
                        setImage(e.target.value);
                      }}
                    />
                  </AtomWrapper>
                  <AtomWrapper flexDirection="row" justifyContent="flex-end">
                    <AtomButton
                      customCSS={css`
                        padding: 5px;
                        background-color: #000000;
                      `}
                      onClick={async () => {
                        if (typeof image === 'string') {
                          editor
                            ?.chain()
                            .focus()
                            .setImage({ src: image })
                            .run();
                          setImage({} as File);
                          setLoadingFile(true);
                          setTimeout(() => {
                            setLoadingFile(false);
                            setIsOpen(false);
                          }, 500);
                        } else if (image.name) {
                          setLoadingFile(true);
                          await uploadImage(image, {
                            name: 'textbox',
                            orgcode: 'IXU-0001',
                          })
                            .then((res) => {
                              setLoadingFile(false);
                              setIsOpen(false);
                              editor
                                ?.chain()
                                .focus()
                                .setImage({ src: res })
                                .run();
                              setImage({} as File);
                            })
                            .catch((err) => {
                              setLoadingFile(false);
                              console.warn(err);
                            });
                        }
                        setIsOpen(false);
                      }}
                    >
                      <AtomIcon
                        height="16px"
                        width="16px"
                        color="white"
                        icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/assets/svgs/commons/check-4787.svg"
                      />
                    </AtomButton>
                  </AtomWrapper>
                </>
              )}
            </AtomWrapper>
          )}
        </ButtonStyled>
        <ButtonStyled
          customCSS={css`
            position: relative;
          `}
          onClick={() => {
            setIsOpenUrl(true);
          }}
        >
          <AtomIcon
            height="15px"
            width="15px"
            color="#373737"
            icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/link-solid.svg"
          />
          {isOpenUrl && (
            <AtomWrapper
              refObject={ref}
              customCSS={css`
                position: absolute;
                background-color: #ffffff;
                padding: 10px 30px;
                border-radius: 5px;
                height: max-content;
                width: 400px;
                max-width: 400px;
                top: 170%;
                left: 50%;
                transform: translateX(-50%);
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.227);
                :after {
                  content: '';
                  position: absolute;
                  top: -10px;
                  left: 50%;
                  transform: translateX(-50%);
                  width: 0;
                  height: 0;
                  border-left: 10px solid transparent;
                  border-right: 10px solid transparent;
                  border-bottom: 10px solid #ffffff;
                }
              `}
            >
              <AtomText
                fontSize="12px"
                fontWeight={600}
                margin="0px 15px 0px 0px"
              >
                Url:
              </AtomText>
              <AtomInput
                customCSS={css`
                  span {
                    display: none;
                  }
                `}
                labelWidth="100%"
                width="90%"
                type="text"
                value={url as string}
                onChange={(e) => {
                  setUrl(e.target.value);
                }}
              />
              <AtomWrapper
                flexDirection="row"
                justifyContent="space-between"
                alignItems="center"
                width="30%"
              >
                <AtomButton
                  onClick={() => {
                    editor
                      ?.chain()
                      .focus()
                      .extendMarkRange('link')
                      .unsetLink()
                      .run();
                    setUrl('');
                    setTimeout(() => {
                      setIsOpenUrl(false);
                    }, 500);
                  }}
                  customCSS={css`
                    padding: 5px;
                    border: 1px solid #000000;
                    color: #000000;
                    background-color: transparent;
                  `}
                >
                  <AtomIcon
                    height="15px"
                    width="15px"
                    color="#000000"
                    customCSS={css`
                      svg {
                        fill: none;
                      }
                    `}
                    icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/assets/svgs/commons/trash.svg"
                  />
                </AtomButton>
                <AtomButton
                  customCSS={css`
                    padding: 5px;
                    background-color: #000000;
                  `}
                  onClick={() => {
                    setTimeout(() => {
                      setIsOpenUrl(false);
                    }, 500);
                    if (url) {
                      editor
                        ?.chain()
                        .focus()
                        .extendMarkRange('link')
                        .setLink({ href: url })
                        .run();
                    }
                  }}
                >
                  <AtomIcon
                    height="16px"
                    width="16px"
                    color="white"
                    icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/assets/svgs/commons/check-4787.svg"
                  />
                </AtomButton>
              </AtomWrapper>
            </AtomWrapper>
          )}
        </ButtonStyled>

        <ButtonStyled
          onClick={() => editor?.chain().focus().toggleBold().run()}
          isActive={editor?.isActive('bold')}
        >
          <AtomIcon
            height="12px"
            width="12px"
            color={editor?.isActive('bold') ? '#fafafa' : '#373737'}
            icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/bold-solid.svg"
          />
        </ButtonStyled>
        <ButtonStyled
          onClick={() => editor?.chain().focus().toggleItalic().run()}
          isActive={editor?.isActive('italic')}
        >
          <AtomIcon
            height="12px"
            width="12px"
            color={editor?.isActive('italic') ? '#fafafa' : '#373737'}
            icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/italic-solid.svg"
          />
        </ButtonStyled>
        <ButtonStyled
          onClick={() => editor?.chain().focus().toggleUnderline().run()}
          isActive={editor?.isActive('underline')}
        >
          <AtomIcon
            height="12px"
            width="12px"
            color={editor?.isActive('underline') ? '#fafafa' : '#373737'}
            icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/underline-solid.svg"
          />
        </ButtonStyled>
        <InputColorStyled
          type="color"
          onInput={(event) =>
            editor
              ?.chain()
              .focus()
              .setColor(`${event.currentTarget.value}`)
              .run()
          }
          value={editor?.getAttributes('textStyle').color ?? '#ffffff'}
        />
        {!notIncludeSelectFonts && (
          <AtomInput
            type="select"
            border="1px solid #d5d5d5"
            height="30px"
            labelWidth="100px"
            options={[
              { id: '0', value: 'Montserrat', label: 'Montserrat' },
              { id: '1', value: 'Inter', label: 'Inter' },
              {
                id: '2',
                value: 'Comic Sans MS, Comic Sans',
                label: 'Comic Sans MS, Comic Sans',
              },
              { id: '3', value: 'Arial', label: 'Arial' },
              { id: '4', value: 'serif', label: 'serif' },
              { id: '5', value: 'monospace', label: 'monospace' },
              { id: '6', value: 'cursive', label: 'cursive' },
            ]}
            customCSS={css`
              select {
                border: none;
                font-size: 10px;
                option {
                  font-size: 10px;
                }
              }
              span {
                display: none;
              }
            `}
            onChange={(e) =>
              editor?.chain().focus().setFontFamily(`${e.target.value}`).run()
            }
          />
        )}
        <AtomInput
          type="select"
          border="1px solid #d5d5d5"
          height="30px"
          labelWidth="55px"
          value={editor?.getAttributes('heading').level ?? '6'}
          options={[
            { id: '0', value: '6', label: '14' },
            { id: '1', value: '5', label: '16' },
            { id: '2', value: '4', label: '18' },
            { id: '3', value: '3', label: '20' },
            { id: '4', value: '2', label: '22' },
            { id: '5', value: '1', label: '24' },
          ]}
          customCSS={css`
            select {
              border: none;
              font-size: 10px;
              option {
                font-size: 10px;
              }
            }
            span {
              display: none;
            }
          `}
          onChange={(e) =>
            editor
              ?.chain()
              .focus()
              .toggleHeading({ level: Number(e.target.value) as unknown as 1 })
              .run()
          }
        />

        <ButtonStyled
          onClick={() => editor?.chain().focus().setTextAlign('left').run()}
          isActive={editor?.isActive({ textAlign: 'left' })}
        >
          <AtomIcon
            height="12px"
            width="12px"
            color={
              editor?.isActive({ textAlign: 'left' }) ? '#fafafa' : '#373737'
            }
            icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/align-left-solid.svg"
          />
        </ButtonStyled>
        <ButtonStyled
          onClick={() => editor?.chain().focus().setTextAlign('center').run()}
          isActive={editor?.isActive({ textAlign: 'center' })}
        >
          <AtomIcon
            height="12px"
            width="12px"
            color={
              editor?.isActive({ textAlign: 'center' }) ? '#fafafa' : '#373737'
            }
            icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/align-center-solid.svg"
          />
        </ButtonStyled>
        <ButtonStyled
          onClick={() => editor?.chain().focus().setTextAlign('right').run()}
          isActive={editor?.isActive({ textAlign: 'right' })}
        >
          <AtomIcon
            height="12px"
            width="12px"
            color={
              editor?.isActive({ textAlign: 'right' }) ? '#fafafa' : '#373737'
            }
            icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/align-right-solid.svg"
          />
        </ButtonStyled>
        <ButtonStyled
          onClick={() => editor?.chain().focus().setTextAlign('justify').run()}
          isActive={editor?.isActive({ textAlign: 'justify' })}
        >
          <AtomIcon
            height="12px"
            width="12px"
            color={
              editor?.isActive({ textAlign: 'justify' }) ? '#fafafa' : '#373737'
            }
            icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/align-justify-solid.svg"
          />
        </ButtonStyled>

        <ButtonStyled
          onClick={() => editor?.chain().focus().toggleBulletList().run()}
          className={editor?.isActive('bulletList') ? 'is-active' : ''}
        >
          <AtomIcon
            height="16px"
            width="16px"
            color="#373737"
            icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/list-ul-solid.svg"
          />
        </ButtonStyled>
        <ButtonStyled
          onClick={() => editor?.chain().focus().toggleOrderedList().run()}
          className={editor?.isActive('orderedList') ? 'is-active' : ''}
        >
          <AtomIcon
            height="16px"
            width="16px"
            color="#373737"
            icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/list-ol-solid.svg"
          />
        </ButtonStyled>
        {!notincludeTableOptions && (
          <>
            <ButtonStyled
              onClick={() =>
                editor
                  ?.chain()
                  .focus()
                  .insertTable({ rows: 3, cols: 3, withHeaderRow: true })
                  .run()
              }
            >
              <AtomIcon
                height="16px"
                width="16px"
                color="#373737"
                icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/table-solid.svg"
              />
              {/* insertTable */}
            </ButtonStyled>
            <ButtonStyled
              onClick={() => editor?.chain().focus().deleteTable().run()}
              disabled={!editor?.can().deleteTable()}
            >
              <AtomIcon
                height="16px"
                width="16px"
                color="#a0a0a0"
                icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/table-solid.svg"
              />
              {/* deleteTable */}
            </ButtonStyled>
            <ButtonStyled
              onClick={() => editor?.chain().focus().addColumnBefore().run()}
              disabled={!editor?.can().addColumnBefore()}
            >
              <AtomIcon
                height="16px"
                width="16px"
                color="#373737"
                icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/border-left-new.svg"
              />
              {/* addColumnBefore */}
            </ButtonStyled>
            <ButtonStyled
              onClick={() => editor?.chain().focus().deleteColumn().run()}
              disabled={!editor?.can().deleteColumn()}
            >
              <AtomIcon
                height="16px"
                width="16px"
                color="#a0a0a0"
                icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/border-center-v.svg"
              />
              {/* deleteColumn */}
            </ButtonStyled>
            <ButtonStyled
              onClick={() => editor?.chain().focus().addColumnAfter().run()}
              disabled={!editor?.can().addColumnAfter()}
            >
              <AtomIcon
                height="16px"
                width="16px"
                color="#373737"
                icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/border-right.svg"
              />
              {/* addColumnAfter */}
            </ButtonStyled>

            <ButtonStyled
              onClick={() => editor?.chain().focus().addRowBefore().run()}
              disabled={!editor?.can().addRowBefore()}
            >
              <AtomIcon
                height="16px"
                width="16px"
                color="#373737"
                icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/border-top.svg"
              />
              {/* addRowBefore */}
            </ButtonStyled>
            <ButtonStyled
              onClick={() => editor?.chain().focus().deleteRow().run()}
              disabled={!editor?.can().deleteRow()}
            >
              <AtomIcon
                height="16px"
                width="16px"
                color="#a0a0a0"
                icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/border-center-h.svg"
              />
              {/* deleteRow */}
            </ButtonStyled>
            <ButtonStyled
              onClick={() => editor?.chain().focus().addRowAfter().run()}
              disabled={!editor?.can().addRowAfter()}
            >
              <AtomIcon
                height="16px"
                width="16px"
                color="#373737"
                icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/IXU-0001/TextEditor/border-bottom.svg"
              />
              {/* addRowAfter */}
            </ButtonStyled>
          </>
        )}
      </AtomWrapper>
    </>
  );
};

const AtomTextEditor: FC<AtomTextEditorType> = (props) => {
  const { id, content, onUpdate, options } = props;

  const editor = useEditor(
    {
      extensions: [
        TextAlign.configure({
          types: ['heading', 'paragraph'],
        }),
        StarterKit,
        TextStyle,
        FontFamily,
        Color,
        UnderLine,
        Image,
        Iframe,
        VideoCustom,
        Table.configure({
          resizable: true,
        }),
        TableRow,
        TableHeader,
        TableCell,
        Link.configure({
          openOnClick: false,
        }),
      ],
      onUpdate: ({ editor: EditorUpdate }) => onUpdate?.(EditorUpdate),
      content: content ?? '',
    },
    [content]
  );

  return (
    <AtomWrapper
      margin="0px 0px"
      width="100%"
      customCSS={css`
        background-color: #ffffff;
        border-radius: 8px;
        box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.052);
        ${props?.onCustomCSSMain?.()}
      `}
    >
      <GlobalStyles />
      <MenuBar
        editor={editor}
        options={options}
        onCustomCSSMenuBar={props?.onCustomCSSMenuBar}
      />
      <AtomWrapper
        customCSS={css`
          min-height: 500px;
          border-top: 1px solid #010101;
          border-radius: 0px 0px 8px 8px;
          z-index: -1;
          align-items: center;
          justify-content: flex-start;
          > div {
            padding: 0px;
            width: 100%;
            height: 100%;
            overflow-y: auto;
          }

          .ProseMirror {
            min-height: 500px;
            padding: 20px 20px;
            outline: none;
            .video-wrapper {
              margin: 0px 0px 20px 0px;
              video {
                width: 100%;
              }
            }
            .iframe-wrapper {
              margin: 0px 0px 20px 0px;
              position: relative;
              padding-bottom: math.div(100, 16) * 9%;
              height: 0;
              overflow: hidden;
              width: 100%;
              height: 300px;

              &.ProseMirror-selectednode {
                outline: 3px solid #68cef8;
              }

              iframe {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
              }
            }
          }
          ${props?.onCustomCSSContent?.()}
        `}
      >
        <EditorContent id={id} editor={editor} />
      </AtomWrapper>
    </AtomWrapper>
  );
};

export default AtomTextEditor;
